import React from 'react'
import  {useState} from 'react';

export const AddTodo = (addTodo) => {
    const [title, setTitle] = useState(" ");
    const [desc, setDesc] = useState(" ");
    const submit = (e)=>{
        e.preventDefault();
        if(!title || !desc){
            alert('plz input title or description');
        }
        else{
            console.log(addTodo);
            addTodo.addTodo(title,desc)
        }
    }
    return (
        <div className="container mt-3 pt-5 mb-3">
            <h3>Add a todo item</h3>
            <form  onSubmit={submit}>
                <div className="mb-3">
                    <label htmlFor="title" className="form-label">Title</label>
                    <input type="text" value={title} onChange={(e)=>{setTitle(e.target.value)}} className="form-control" id="title" aria-describedby="emailHelp" />
                    {/* <div id="emailHe lp" className="form-text">We'll never share your email with anyone else.</div> */}
                </div>
                <div className="mb-3">
                    <label htmlFor="desc" className="form-label">Description</label>  
                    <input type="text" value={desc} onChange={(e)=>{setDesc (e.target.value)}} className="form-control" id="desc" />
                </div>
                <button type="submit" className="btn btn-sm   btn-success">Add TodoItem</button>
            </form>
        </div>
    )
}
