import './App.css';
import Header from './MyComponents/Header';
import {Footer} from './MyComponents/Footer';
import {Todos} from './MyComponents/Todos';
import {AddTodo} from './MyComponents/AddTodo'
import React,{useState} from 'react';

function App() {

  const addTodo = (title,desc)=>{
    console.log("i am adding this todo: ", ' title-'+title,' description-'+desc);
    let sno;
    if(todos.length === 0){
      sno = 0  ;
      console.log("todos all - ", todos);
    }
    else{
      sno = todos[todos.length-1].sno+1;
    }
    
    const myTodo = {
      sno: sno,
      title: title,
      desc: desc
    }
    console.log("myTodo: ",myTodo);
    console.log("todos: -----",todos);
    if(todos.length === 0 ){
      todos.push(myTodo);
      console.log("todos: -----",todos);
    }
    else{
      setTodos([...todos,myTodo]); 
      console.log("todos all - ", todos);
    }
  }

  const   onDelete  = (todo)=>{
    console.log("I am ondelete of todo", todo);
    setTodos(todos.filter((e)=>{
      return e!== todo; 
    }))
    //deleting this way in react will not work at all
    // let index = todos.indexOf(todo);
    // todos.splice(index,1);
  }
  const  [todos, setTodos] = useState([
    {
      sno: 1,
      title: 'go for gym',
      desc: 'you need to go for gym today'
    },
    {
      sno: 2,
      title: 'go for yoga',
      desc: 'you need to go for yoga today'
    },
    {
      sno: 3,
      title: 'go for study',
      desc: 'you need to go for study today'
    }
  ])
 
  return (
    <>
      <Header title = "Mera Todo" searchBar={false}/>
      <AddTodo  addTodo={addTodo}/>

      <Todos todos={todos} onDelete={onDelete}/>
      <Footer />
    </>
  );
}

export default App;
